@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "Arial";
  font-style: normal;
  font-weight: normal;
  src: local("Arial");
}
body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  background-color: white;
  color: black;
}
img {
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  padding-left: 30px;
  padding-right: 30px;
}
@media (min-width: 768px) {
  .container {
    width: 668px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 0;
    padding-right: 0;
  }
}
@media (min-width: 1150px) {
  .container {
    width: 1050px;
    box-sizing: border-box;
  }
}
.product {
  margin-bottom: 30px;
  position: relative;
}
.product__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
.product__item--light {
  background-color: #63D1BB;
}
.product__item--dark {
  background-color: #56C3B2;
}
.product__item {
  flex-basis: auto;
  width: 100%;
}
.product-link {
  display: flex;
  justify-content: space-between;
  text-decoration: none;
  color: white;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 19px;
  line-height: 24px;
  font-weight: bold;
  min-height: 103px;
  margin: 0;
  padding-top: 25px;
  padding-bottom: 15px;
  padding-left: 30px;
  padding-right: 30px;
  max-height: 103px;
  box-sizing: border-box;
}
.product-link__text {
  white-space: pre-line;
  max-width: 130px;
  align-items: flex-start;
}
@media (min-width: 768px) {
  .product {
    width: auto;
    margin-bottom: 51px;
  }
  .product::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 24px;
    max-height: 34px;
    background-image: url(../img/bg-zigzag-fill.svg);
    background-repeat: no-repeat;
    bottom: -11px;
  }
  .product__list {
    display: flex;
    flex-direction: row;
    padding: 0 50px;
    min-width: 668px;
    margin: 0 auto;
  }
  .product-link {
    font-size: 20px;
    line-height: 30px;
    min-height: 119px;
    max-height: 119px;
    padding-left: 35px;
  }
  .product-link--light:hover {
    padding-bottom: 35px;
    margin-top: -5px;
    background-color: #59D5BC;
  }
  .product-link--dark:hover {
    padding-bottom: 35px;
    margin-top: -5px;
    background-color: #46C1AE;
  }
}
@media (min-width: 1150px) {
  .product::after {
    content: "";
    height: 37px;
    max-height: 37px;
    background-position: 50% 50%;
    background-size: 1250px 100%;
    background-repeat: repeat;
  }
  .product__list {
    margin: 0 auto;
    max-width: 1150px;
    justify-content: center;
  }
  .product__item {
    flex-grow: 1;
    min-height: 115px;
  }
}
.site-navigation {
  display: flex;
  flex-shrink: 0;
  flex-basis: auto;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 17px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
}
.site-navigation__item {
  padding-top: 29px;
  padding-bottom: 27px;
  padding-left: 77px;
  box-shadow: 0 -1px 0 0 #e5e5e5;
}
.site-navigation__link {
  text-decoration: none;
  font-weight: 700;
  color: black;
}
.site-navigation__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.site-navigation__link:active {
  color: rgba(0, 0, 0, 0.3);
}
.site-navigation--lower {
  display: none;
}
@media (min-width: 768px) {
  .site-navigation {
    flex-direction: row;
  }
  .site-navigation__item--receipts,
  .site-navigation__item--sale {
    display: none;
  }
  .site-navigation__item {
    font-size: 15px;
    min-width: 83px;
    box-shadow: none;
  }
  .site-navigation__item--catalog,
  .site-navigation__item--order {
    padding-left: 0;
    padding-top: 17px;
    padding-right: 37px;
    padding-bottom: 25px;
  }
}
@media (min-width: 1150px) {
  .site-navigation__item--receipts,
  .site-navigation__item--sale {
    display: flex;
    padding: 0;
    padding-right: 55px;
  }
  .site-navigation {
    justify-content: flex-start;
    grid-column-start: 1;
    grid-row-start: 1;
    align-items: center;
  }
  .site-navigation__link--catalog,
  .site-navigation__link--order {
    font-size: 17px;
  }
  .site-navigation__item--catalog,
  .site-navigation__item--order {
    padding-top: 23px;
    padding-right: 35px;
  }
  .site-navigation__item--order {
    padding-left: 36px;
    padding-right: 0;
    padding-top: 34px;
    padding-bottom: 31px;
    box-shadow: inset 1px 0 0 0 #e5e5e5;
  }
  .site-navigation--lower {
    display: flex;
    grid-column-start: 1;
    grid-row-start: 2;
  }
  .site-navigation--lower {
    max-height: 40px;
    align-items: flex-end;
  }
  .site-navigation__item--receipts,
  .site-navigation__item--sale {
    padding-right: 62px;
  }
}
.about {
  margin: 0;
}
.about__title {
  margin: 0;
  margin-bottom: 28px;
  text-align: left;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
}
.about__list {
  list-style-type: none;
  text-align: left;
  padding: 0;
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 20px;
}
.about__text {
  margin: 0;
}
.about__item {
  margin-bottom: 30px;
}
.about__item:last-child {
  margin-bottom: 61px;
}
.about__item .about__svg {
  fill: #63D1BB;
  margin-bottom: 15px;
  padding-left: 9px;
}
.about__svg--second {
  padding-left: 3px;
  fill: #63D1BB;
  margin-bottom: 20px;
}
.about__item--second {
  margin-bottom: 33px;
}
.about__svg--third {
  padding-left: 9px;
  fill: #63D1BB;
  margin-bottom: 19px;
}
.about__svg--fifth {
  fill: #63D1BB;
  margin-bottom: 24px;
}
.about__svg--sixth {
  fill: #63D1BB;
  margin-bottom: 18px;
  padding-left: 3px;
}
@media (min-width: 768px) {
  .about {
    margin-bottom: 21px;
  }
  .about__inner {
    width: 700px;
    padding-left: auto;
  }
  .about__title {
    font-size: 43px;
    line-height: 50px;
    padding-left: 15px;
  }
  .about__list {
    font-size: 20px;
    line-height: 22px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    margin: 0;
    padding-top: 14px;
  }
  .about__item {
    margin: 0;
  }
  .about__item:nth-child(-n+3) {
    padding-left: 15px;
  }
  .about__item--second {
    grid-row-start: 2;
  }
  .about__item--third {
    grid-row-start: 3;
  }
  .about__svg--sixth {
    margin-bottom: 15px;
  }
}
@media (min-width: 1150px) {
  .about {
    width: auto;
    margin-bottom: 27px;
  }
  .about__inner {
    width: auto;
    max-width: 1150px;
    padding-left: 210px;
    padding-right: 100px;
  }
  .about__list {
    margin-bottom: 0;
    grid-template-columns: 365px 460px;
  }
  .about__title {
    font-size: 53px;
    line-height: 57px;
  }
}
.featured {
  margin-bottom: 36px;
  position: relative;
}
.featured::after {
  content: "";
  display: block;
  position: absolute;
  background-image: url(../img/bg-zigzag-line.svg);
  height: 6px;
  width: 100%;
  background-repeat: repeat-x;
  background-size: auto 100%;
  transform: rotate(180deg);
}
.featured__inner {
  text-align: center;
}
.featured__title {
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  text-align: left;
  margin: 0;
  margin-bottom: 5px;
}
.featured__name {
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 20px;
  color: #63D1BB;
  text-transform: uppercase;
  text-align: left;
  margin: 0;
  margin-bottom: 17px;
}
.featured__content-text {
  margin-bottom: 28px;
}
.featured__description {
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 24px;
  text-align: left;
  margin: 0;
  margin-bottom: 20px;
}
.featured__parameters {
  width: 100%;
  font-size: 15px;
  line-height: 20px;
  margin-bottom: 30px;
  border-collapse: collapse;
}
.featured__row {
  margin: 0;
  padding: 0;
  border-bottom: 1px solid #ECECEC;
}
.featured__element {
  padding-left: 0;
  padding-top: 7px;
  padding-bottom: 11px;
  padding-right: 5px;
  text-align: left;
}
.featured__parameter {
  padding: 0;
  padding-top: 7px;
  padding-bottom: 11px;
  text-align: left;
}
.featured__cost {
  width: 154px;
  margin-right: auto;
  margin-left: auto;
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  margin-top: 20px;
  margin-bottom: 24px;
}
.featured__picture {
  width: 100%;
  height: auto;
  min-height: 315px;
}
.featured__button {
  width: 99.8%;
  font-family: inherit;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  border: 2px solid #444444;
  padding-top: 16px;
  padding-bottom: 14px;
  margin-bottom: 36px;
  text-decoration: none;
  text-transform: uppercase;
  color: #444444;
  box-sizing: border-box;
}
.featured__button:hover {
  color: #ffffff;
  background-color: #444444;
}
.featured__button:active {
  color: rgba(225, 225, 225, 0.3);
}
@media (min-width: 768px) {
  .featured {
    margin-bottom: 124px;
  }
  .featured::after {
    content: "";
    display: block;
    position: absolute;
    bottom: -65px;
    background-image: url(../img/bg-zigzag-line.svg);
    height: 17px;
    width: 100%;
    background-repeat: repeat-x;
    background-size: auto 100%;
