  }
  .featured__content-text {
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 2;
    margin: 0;
    padding-top: 36px;
    margin-bottom: 88px;
  }
  .featured__description {
    font-size: 17px;
    line-height: 30px;
    margin-bottom: 52px;
  }
  .featured__parameters {
    margin-bottom: 20px;
  }
  .featured__picture {
    grid-column-start: 4;
    grid-column-end: -1;
    grid-row-start: 1;
    grid-row-end: -1;
