body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  font-family: @base-font;
  font-size: 17px;
  line-height: 24px;
  background-color: white;
  color: black;
}

img {
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
